* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; }
html {
    height: 100%; }
body {
    height: 100%; }

.bg {
    background-image: url("/public/img/hiking.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center; }
.container {
    display: flex; }
.wrapper {
    display: flex; }
.justify-content {
    display: flex;
    justify-content: center; }
.row {
    flex-direction: row; }
.header__logo {
    width: 100%;
    margin: 0 auto; }
.filter-section {
    display: flex;
    align-items: center;
    height: 100%; }
.filter__form-list {}

.filter__form-item-dropdown {
    list-style: none; }

